import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 引入移动适配模块
import 'amfe-flexible'

// 引入vant组件并安装
import { NavBar, Form, Field, CellGroup, Button, Tabbar, TabbarItem, Icon, Tab, Tabs, Cell, List, PullRefresh, ActionSheet, Popup, Row, Col, Badge, Search, Divider, Tag, Image, Dialog, DatetimePicker } from 'vant'

Vue.use(DatetimePicker)
Vue.use(Dialog)
Vue.use(Image)
Vue.use(Tag)
Vue.use(Divider)
Vue.use(Search)
Vue.use(Row)
Vue.use(Col)
Vue.use(Badge)
Vue.use(Popup)
Vue.use(NavBar)
Vue.use(Form)
Vue.use(Field)
Vue.use(CellGroup)
Vue.use(Button)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Icon)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Cell)
Vue.use(List)
Vue.use(PullRefresh)
Vue.use(ActionSheet)

Vue.config.productionTip = false

// 创建全局自定义指令（搜索组件input自动聚焦）
Vue.directive('selfFocus', {
  inserted(el) {
    if (el.nodeName === 'TEXTAREA' || el.nodeName === 'INPUT') {
      el.focus()
    } else if (el.querySelector('input')) {
      el.querySelector('input').focus()
    } else {
      el.querySelector('textarea').focus()
    }
  },
  update(el) {
    if (el.nodeName === 'TEXTAREA' || el.nodeName === 'INPUT') {
      el.focus()
    } else if (el.querySelector('input')) {
      el.querySelector('input').focus()
    } else {
      el.querySelector('textarea').focus()
    }
  }
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
