import Vue from 'vue'
import VueRouter from 'vue-router'

// 导入组件
// import Login from '@/views/Login/Login.vue'
// import Layout from '@/views/Layout/index.vue'
// import Home from '@/views/Home/index.vue'
// import User from '@/views/User/index.vue'
// import Search from '@/views/Search/index.vue'
// import SearchResult from '@/views/Search/SearchResult.vue'
// import ArticleDetail from '@/views/ArticleDetail/index.vue'
// import UserEdit from '@/views//User/UserEdit.vue'
// import Chat from '@/views/Chat/index.vue'

import { getToken } from '@/utils/token'

Vue.use(VueRouter)

// 路由规则
const routes = [
  // 首页进入redirect跳转到home页面
  { path: '/', redirect: '/layout/home' },
  { path: '/login', component: () => import(/* webpackChunkName: "Login" */ '@/views/Login/Login.vue') },
  // 页面Layout路由规则
  {
    path: '/layout',
    component: () => import(/* webpackChunkName: "Layout" */ '@/views/Layout/index.vue'),
    // 进入layout页面再自动跳转到home子路由中
    redirect: 'home',
    // layout页面又有俩子路由User和Home
    children: [
      { path: 'home', component: () => import(/* webpackChunkName: "Home" */ '@/views/Home/index.vue') },
      { path: 'user', component: () => import(/* webpackChunkName: "User" */ '@/views/User/index.vue') }
    ]
  },
  {
    path: '/search',
    component: () => import(/* webpackChunkName: "Search" */ '@/views/Search/index.vue')
  },
  { // 搜索结果页
    path: '/search_result/:kw',
    component: () => import(/* webpackChunkName: "SearchResult" */ '@/views/Search/SearchResult.vue'),
    props: true
  },
  { // 文章详情
    path: '/detail',
    component: () => import(/* webpackChunkName: "ArticleDetail" */ '@/views/ArticleDetail/index.vue')
  },
  // 用户编辑页面
  {
    path: '/user_editor',
    component: () => import(/* webpackChunkName: "UserEdit" */ '@/views//User/UserEdit.vue')
  },
  // 小思同学聊天
  {
    path: '/chat',
    component: () => import(/* webpackChunkName: "Chat" */ '@/views/Chat/index.vue')
  }
]

const router = new VueRouter({
  routes
})

// 路由的全局守卫,还可以在某个路由规则中单独设置私有守卫，表示去到该路由beforeEnter(to,from,next)
router.beforeEach((to, from, next) => {
  // 已经有token并且还要去登录页面,阻止跳转（只有退出了登录，才能去登录页）
  if (getToken()?.length > 0 && to.path === '/login') {
    // 强制跳转到首页
    next('/layout/home')
  } else {
    next()
  }
})

export default router
