// 封装三个方法，专门用于操作后端返回的token.代码结构更加清晰

// 定义变量作为token的名字
const key = 'geek-itheima'

// 保存token方法
export const setToken = (token) => {
  // 引入该模块时传token参数过来，然后setItem保存token值
  // key是名，token是实际token值
  localStorage.setItem(key, token)
}

// 获取token方法(注意获取的是值，为了调用该方法的地方不再用变量接收，这里直接把值return出去，所以这里是把tokenreturn出去的，return省略了而已)
export const getToken = () =>
  localStorage.getItem(key)

// 删除token方法
export const removeToken = () => {
  localStorage.removeItem(key)
}
